import React from 'react';
import stc from "../img/stc.jpg"
const App = () => {
  return (
    <div>
        <div className="container">
          <h1 className="santa">DANZA EN SANTA CRUZ</h1>
        </div>
      <div className="container mt-4">
            <div className="row">
                <div className="his">
                    <p>
                    La danza Carnavalito es una baile tpico del departamento de Santa Cruz, nacida al calor del Huayo Valluno y la tradicional Jota espaola durante el ocaso del coloniaje y el siglo XIX. Pertenece al grupo de las grandes danzas colectivas, es jovial, se ejecuta alegremente a modo de juego inocente y familiar.
                    </p>
                    <h2 className='subs'>LA DANZA</h2>
              <p>
              Es el nombre de una antigua danza que practican pueblos indgenas del Altiplano, mas precisamente los indgenas Quechuas. Suelen bailarla en cualquier poca del ao. Su msica es alegre, en general pentatnica, ejecutada con instrumentos regionales como la quena, el charango, el erke, la caja chayera, el sikus y el bombo.

Primitivamente se bail como danza colectiva, sin parejas, con las pocas figuras comunes a casi todos los bailes arcaicos: rondas, filas, serpentinas, etc. Posteriormente, se continu bailando en grupo pero tambin agrupados en parejas, realizando movimientos al comps de la msica. Los danzantes se mueven en torno a los msicos o en hilera y una mujer u hombre con un estandarte o bandern adornado con cintas en la mano, se encarga de dirigir la hilera.
              </p>
                </div>
                    
                </div>
          </div>

      <div className='fusions'>
          <div className="stc1">
              <div className="imagen-container"><img src={stc} alt="santa cruz" /></div>
          </div>
        
        <div className="stc2">
              <h2 className='subs'>COREOGRAFIA</h2>
              <p>
              El baile de la danza Carnavalito posee elementos simples que van disponindonos a gusto de los bailarines, tal es el caso de las hileras o filas, de los puentes y las diferentes tomas y enlaces entre las parejas. Cada elemento es utilizado dentro de la danza desde la propia iniciativa del grupo danzante. Como por ejemplo las Calles se generan para que las dems parejas pasen por el pasillo central y cada una de esas parejas decide como atravesar la calle. Esto puede ser imitando algn movimiento, haciendo morisquetas, girando, saltando, todo desde un sentido alegre.
              </p>
              <h2 className='subs'>VESTIMENTA</h2>
              <p><strong>Las mujeres:</strong> usan tipoy (vestido largo y sin mangas usado por las indgenas orientales) y se adornan la cabeza con flores multicolores tpicas de la regin.</p>
              <p><strong>Los varones:</strong> usan camisa blanca, pantaln hasta el tobillo, el sombrero de sao y la paoleta al cuello.
              </p>

        </div>

      </div>
    </div>
      
  );
};

export default App;