import React from 'react';
import cocha from "../img/cocha.jpg"
const App = () => {
  return (
    <div>
        <div className="container">
          <h1 className="cocha">DANZA EN COCHABAMBA</h1>
        </div>
      <div className="container mt-4">
            <div className="row">
                <div className="his">
                    <p>
                    La danza cueca cochabambina es una danza popular de Cochabamba, es un juego de seduccin entre un hombre y una mujer. La mano derecha en la cintura y con la izquierda bate el pauelo acosando a la dama, mientras ella juega a la indiferencia siguiendo coqueta el baile de su pareja.

                    En general esta danza es muy popular en toda Bolivia, tiene como ancestro al zapateo espaol, en la poca de la colonia estuvo reservada para la clase aristocrtica de dicho periodo y tambin de los primeros aos de la independencia boliviana. 
                    </p>
                    <h2 className='sub'>HISTORIA DE LA CUECA</h2>
              <p>
              El origen de la cueca boliviana se remonta a la poca colonial, siendo resultado de la mezcla de lo indgena y lo espaol. Durante la Guerra del Chaco (1932-1935), un momento histrico importante para la consolidacin de la identidad criollo-mestiza, la cueca se transform en un discurso patritico que aviv y exalt la moral de los soldados y de la sociedad boliviana. Desde entonces, la letra de la cueca boliviana es potica y profunda, evoca pocas histricas, historias de amor y desamor y sentimientos nacionales.
              </p>
                </div>
                    
                </div>
          </div>

      <div className='fusion'>
          <div className="cocha1">
              <div className="imagen-container"><img src={cocha} alt="cochabamba" /></div>
          </div>
        
        <div className="cocha2">
              <h2 className='sub'>COREOGRAFIA</h2>
              <p>
              En la coreografa de la danza cueca cochabambina destaca el lenguaje corporal y el encuentro de miradas. Durante el baile se traza figuras circulares, con vueltas, medias vueltas para concluir en un zapateo.
              La cueca consta de cuatro momentos dividido en ocho compases: la Introduccin, el Adentro, la Quimba y el Jaleo. La Quimba es el momento del mximo de la cueca en el que el bailarn coquetea a la mujer y ella lo esquiva varias veces escondiendo su rostro detrs del pauelo. Este momento es acompaado con palmas por el pblico espectador.
              Lo fundamental de la cueca boliviana, que distingue a otras cuecas de la regin, es la tpica repeticin total del baile en sus cuatro momentos o lo denominado coloquialmente como <strong>la segundita</strong>.
              </p>
              <p>
              La danza es un elemento muy importante dentro de la cultura boliviana existiendo festividades masivas y colectivas denominada entradas donde conjuntos y fraternidades ocupan espacios pblicos para danzar, este fenmeno social solo ocurre en esa magnitud en Bolivia existiendo ms de 1200 festividades dentro del pas que se repiten anualmente donde se interpretan diversas danzas bolivianas,
              </p>
        </div>

      </div>
    </div>
      
  );
};

export default App;